.user-form{
	@include flex(center, center);
	@include layout(50%, calc(100vh - 80px));
	background-color: $primary-20;

	.user-form--wrap{
		@include flex(flex-start, flex-start, column);
		gap: 30px;
		@include layout(600px, 650px);
		padding: 50px;
		background-color: #fff;
		border-radius: 20px;
		box-shadow: 0px 0px 20px 0px #d9d9d9;

		> div{
			@include flex(flex-start, flex-start, column);
			@include layout(100%, auto);
			gap: 20px;
		}
		.user-form--title{
			position: relative;
			@include font($ns, 24px);

			&:before{
				content: '';
				position: absolute;
				top: 0px;
				right: -7px;
				@include layout(3px, 3px);
				background-color: #f00;
				border-radius: 50%;
			}
		}
		.user-form--input{
			@include layout(100%, 45px);
			padding: 10px;
			@include border(1px, $primary-60, 6px);
			@include font($ns, 14px, $primary);
		
			&::placeholder{
				@include font($ns, 14px, #959595);
			}
		}
		.user-form--radio-wrap{
			@include flex(center, flex-start);
			gap: 12px;

			> div{
				@include flex(center, flex-start);
				gap: 10px;

				input[type=radio]{
					@include layout(20px, 20px);
				}
				label{
					font-size: 20px;
					user-select: none;
				}
			}
		}
	}
}