.load-wrap{
    @include flex(center, center, column);
    gap: 40px;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 99999;
    @include layout(100%, 100vh);
    background-color: #fff;

    .load-img{
        width: 100px;
        animation: loading-img 2s infinite;
    }
    .load-text{
        @include font($ns, 32px);
        span{
            display: inline-block;

            &:nth-child(1){animation: loading-text 1.5s infinite 0.2s;}
            &:nth-child(2){animation: loading-text 1.5s infinite 0.4s;}
            &:nth-child(3){animation: loading-text 1.5s infinite 0.6s;}
            &:nth-child(10){animation: loading-text 1.5s infinite 0.8s;}
            &:nth-child(11){animation: loading-text 1.5s infinite 1s;}
            &:nth-child(12){animation: loading-text 1.5s infinite 1.2s;}
        }
    }
}

@keyframes loading-img {
    0%{transform: translateY(0%);}
    50%{transform: translateY(-30px);}
    100%{transform: translateY(0%);}
}

@keyframes loading-text {
    0%{transform: translateY(0%);}
    50%{transform: translateY(-10px);}
    100%{transform: translateY(0%);}
}