header{
    width: 100%;
    height: 80px;
    box-shadow: 0px 2px 20px #efefef;

    .inner{
        @include flex(center, space-between);

        .logo{
            @include layout(80px, 80px);
            border-radius: 12px;
            cursor: pointer;
        }
        nav{
            ul{
                @include flex(center, flex-start);
                gap: 32px;

                li{
                    a{
                        font-size: 20px;
                        font-weight: 600;
                        color: $primary;
                    }
                }
            }
        }
    }
}