html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  box-sizing: border-box;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

input {
  box-sizing: border-box;
}

input:focus {
  outline: none;
}

a {
  color: #000;
  text-decoration: none;
}

article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}
.App {
  width: 100%;
  height: 100%;
}

body {
  font-family: "Noto Sans KR";
}

.inner {
  width: 1500px;
  margin: auto;
}

.c-radio {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  border: 1px solid #959595;
  border-radius: 50%;
}

.c-radio.clicked {
  border: 2px solid #45658e;
  border-radius: 50%;
}

.submit-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  width: 130px;
  height: 50px;
  margin: auto;
  margin-top: 45px;
  background-color: #45658e;
  border: 0 solid #fff;
  border-radius: 6px;
  font-family: "Noto Sans KR";
  font-size: 18px;
  color: #fff;
  cursor: pointer;
}

header {
  width: 100%;
  height: 80px;
  box-shadow: 0px 2px 20px #efefef;
}
header .inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: row;
}
header .inner .logo {
  width: 80px;
  height: 80px;
  border-radius: 12px;
  cursor: pointer;
}
header .inner nav ul {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-direction: row;
  gap: 32px;
}
header .inner nav ul li a {
  font-size: 20px;
  font-weight: 600;
  color: #45658e;
}

main {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  width: 100%;
  height: auto;
}
main .main-img {
  width: 50%;
  height: calc(100vh - 80px);
}

.user-form {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  width: 50%;
  height: calc(100vh - 80px);
  background-color: rgba(69, 101, 142, 0.1254901961);
}
.user-form .user-form--wrap {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  flex-direction: column;
  gap: 30px;
  width: 600px;
  height: 650px;
  padding: 50px;
  background-color: #fff;
  border-radius: 20px;
  box-shadow: 0px 0px 20px 0px #d9d9d9;
}
.user-form .user-form--wrap > div {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  flex-direction: column;
  width: 100%;
  height: auto;
  gap: 20px;
}
.user-form .user-form--wrap .user-form--title {
  position: relative;
  font-family: "Noto Sans KR";
  font-size: 24px;
  color: #000;
}
.user-form .user-form--wrap .user-form--title:before {
  content: "";
  position: absolute;
  top: 0px;
  right: -7px;
  width: 3px;
  height: 3px;
  background-color: #f00;
  border-radius: 50%;
}
.user-form .user-form--wrap .user-form--input {
  width: 100%;
  height: 45px;
  padding: 10px;
  border: 1px solid rgba(69, 101, 142, 0.3764705882);
  border-radius: 6px;
  font-family: "Noto Sans KR";
  font-size: 14px;
  color: #45658e;
}
.user-form .user-form--wrap .user-form--input::-moz-placeholder {
  font-family: "Noto Sans KR";
  font-size: 14px;
  color: #959595;
}
.user-form .user-form--wrap .user-form--input::placeholder {
  font-family: "Noto Sans KR";
  font-size: 14px;
  color: #959595;
}
.user-form .user-form--wrap .user-form--radio-wrap {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-direction: row;
  gap: 12px;
}
.user-form .user-form--wrap .user-form--radio-wrap > div {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-direction: row;
  gap: 10px;
}
.user-form .user-form--wrap .user-form--radio-wrap > div input[type=radio] {
  width: 20px;
  height: 20px;
}
.user-form .user-form--wrap .user-form--radio-wrap > div label {
  font-size: 20px;
  -webkit-user-select: none;
     -moz-user-select: none;
          user-select: none;
}

.worldcup-wrap {
  width: 100vw;
  height: calc(100vh - 80px);
  background-color: rgba(69, 101, 142, 0.1254901961);
}

.worldcup-modal--wrap {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 700px;
  height: 400px;
  padding: 40px;
  background-color: #fff;
  border: 0px solid #fff;
  border-radius: 50px;
  transition: all 0.3s;
  box-shadow: 0px 0px 20px 0px #d9d9d9;
}
.worldcup-modal--wrap .worldcup-modal--title {
  margin-bottom: 100px;
  font-size: 24px;
}
.worldcup-modal--wrap .worldcup-modal--title span {
  color: #45658e;
}
.worldcup-modal--wrap .worldcup-modal--text {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  flex-direction: column;
  gap: 10px;
  font-size: 20px;
}

.worldcup-content--wrap {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  width: 100%;
  height: 100%;
  transition: all 0.3s;
  opacity: 0;
}
.worldcup-content--wrap .worldcup-progress {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  position: relative;
  width: 100%;
  height: 80px;
  border-bottom: 1px solid rgba(69, 101, 142, 0.1254901961);
  font-size: 24px;
  font-weight: 400;
}
.worldcup-content--wrap .worldcup-progress--bar {
  position: absolute;
  top: 0;
  left: 0;
  width: 0%;
  height: 3px;
  background-color: #45658e;
  transition: all 0.3s;
}
.worldcup-content--wrap .worldcup-progress--text span {
  font-weight: 600;
  color: #45658e;
}
.worldcup-content--wrap .worldcup-content {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
}
.worldcup-content--wrap .worldcup-content .worldcup-box {
  width: 50vw;
  height: calc(100vh - 150px);
  transition: all 0.3s;
}
.worldcup-content--wrap .worldcup-content .worldcup-box .worldcup-img {
  width: 100%;
  height: 670px;
}
.worldcup-content--wrap .worldcup-content .worldcup-box .worldcup-title {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  width: 100%;
  height: 91px;
  font-size: 24px;
  font-weight: 800;
}
.worldcup-content--wrap .worldcup-content .worldcup-left.selected {
  transform: translateX(50%);
}
.worldcup-content--wrap .worldcup-content .worldcup-left.unselected {
  transform: scale(0);
}
.worldcup-content--wrap .worldcup-content .worldcup-left.none {
  display: none;
}
.worldcup-content--wrap .worldcup-content .worldcup-right.selected {
  transform: translateX(-50%);
}
.worldcup-content--wrap .worldcup-content .worldcup-right.unselected {
  transform: scale(0);
}
.worldcup-content--wrap .worldcup-content .worldcup-right.none {
  display: none;
}

.load-wrap {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  gap: 40px;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 99999;
  width: 100%;
  height: 100vh;
  background-color: #fff;
}
.load-wrap .load-img {
  width: 100px;
  animation: loading-img 2s infinite;
}
.load-wrap .load-text {
  font-family: "Noto Sans KR";
  font-size: 32px;
  color: #000;
}
.load-wrap .load-text span {
  display: inline-block;
}
.load-wrap .load-text span:nth-child(1) {
  animation: loading-text 1.5s infinite 0.2s;
}
.load-wrap .load-text span:nth-child(2) {
  animation: loading-text 1.5s infinite 0.4s;
}
.load-wrap .load-text span:nth-child(3) {
  animation: loading-text 1.5s infinite 0.6s;
}
.load-wrap .load-text span:nth-child(10) {
  animation: loading-text 1.5s infinite 0.8s;
}
.load-wrap .load-text span:nth-child(11) {
  animation: loading-text 1.5s infinite 1s;
}
.load-wrap .load-text span:nth-child(12) {
  animation: loading-text 1.5s infinite 1.2s;
}

@keyframes loading-img {
  0% {
    transform: translateY(0%);
  }
  50% {
    transform: translateY(-30px);
  }
  100% {
    transform: translateY(0%);
  }
}
@keyframes loading-text {
  0% {
    transform: translateY(0%);
  }
  50% {
    transform: translateY(-10px);
  }
  100% {
    transform: translateY(0%);
  }
}
.error-page {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  width: 100%;
  height: 100%;
}
.error-page .error-img {
  width: 650px;
  height: auto;
}
.error-page .error-text {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  gap: 16px;
  font-size: 28px;
  font-weight: 700;
}/*# sourceMappingURL=style.css.map */