.worldcup-wrap{
    @include layout(100vw, calc(100vh - 80px));
	background-color: $primary-20;
}

.worldcup-modal--wrap{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    @include layout(700px, 400px);
    padding: 40px;
    background-color: #fff;
    @include border(0px,  #fff, 50px);
    transition: all 0.3s;
    box-shadow: 0px 0px 20px 0px #d9d9d9;

    .worldcup-modal--title{
        margin-bottom: 100px;
        font-size: 24px;

        span{color: $primary;}
    }
    .worldcup-modal--text{
        @include flex(flex-start, flex-start, column);
        gap: 10px;
        font-size: 20px;
    }
}

.worldcup-content--wrap{
    @include flex(center, center, column);
    @include layout(100%, 100%);
    transition: all 0.3s;
    opacity: 0;

    .worldcup-progress{
        @include flex(center, center);
        position: relative;
        @include layout(100%, 80px);
        border-bottom: 1px solid $primary-20;
        font-size: 24px;
        font-weight: 400;

        &--bar{
            position: absolute;
            top: 0;
            left: 0;
            @include layout(0%, 3px);
            background-color: $primary;
            transition: all 0.3s
        }
        &--text{
            span{
                font-weight: 600;
                color: $primary;
            }
        }
    }
    .worldcup-content{
        @include flex(center, center);

        .worldcup-box{
            @include layout(50vw, calc(100vh - 150px));
            transition: all 0.3s;

            .worldcup-img{
                @include layout(100%, 670px);
            }
            .worldcup-title{
                @include flex(center, center);
                @include layout(100%, 91px);
                font-size: 24px;
                font-weight: 800;
            }
        }
        .worldcup-left{
            // border-right: 1px solid $primary;
        }
        .worldcup-left.selected{
            transform: translateX(50%);
        }
        .worldcup-left.unselected{
            transform: scale(0);
        }
        .worldcup-left.none{
            display: none;
        }
        .worldcup-right.selected{
            transform: translateX(-50%);
        }
        .worldcup-right.unselected{
            transform: scale(0);
        }
        .worldcup-right.none{
            display: none;
        }
    }
}