.c-radio{
    appearance: none;
    @include border(1px, #959595, 50%);
}

.c-radio.clicked{
    @include border(2px, $primary, 50%);
}

.submit-btn{
    @include flex(center, center);
    @include layout(130px, 50px);
    margin: auto;
    margin-top: 45px;
    background-color: $primary;
    @include border(0, #fff, 6px);
    @include font($ns, 18px, #fff);
    cursor: pointer;
}